.header{
	.lockup{
		display: flex;
		align-items: center;
		h5{
			margin-left:1rem;
		}
	}
}

.side-nav{
	display:flex;
	flex-direction:row;
	.nav-title{
		margin-right:1rem;
	}
}

.top-nav, .side-nav{
	display: flex;
	li {
		a{
		    padding: .5rem 1rem;
		    border:1px solid transparent;
		    color:var(--brand-light);
		    opacity:0.6;
		    transition:.5s ease-in-out;
			&.active {
				opacity:1;
				border-bottom:3px solid var(--primary-brand);
				background:rgba(155,155,155,0.1);
			}
			&:hover{
			  border-bottom:3px solid var(--primary-brand);
				opacity:1;
			}
		}
	}
}

@media screen and (max-width:768px){
	.top-nav li a, .side-nav li a{
		padding:.5rem .25rem;
		display:block;
		text-align: center;
	}

	ul.side-nav{
		display:grid;
		overflow-x:scroll;
		grid-template-columns: minmax(70px, 1fr);
		.nav-title{
			grid-row:1;
			padding:1rem 0;
		}
		li{
			grid-row:2;
		}
	}
}
