.twd{
  $font-primary: 'Alternate Gothic W05 No_3', 'Oswald', Helvetica, sans-serif;
  $font-secondary: 'Unitext', Helvetica, sans-serif;
  --primary-dark: black;
  --primary-light: white;
  --primary-brand: #ffc600;
  --accent-dark: #111111;
  --accent-light: #efefef;

  h1, h2, h3, h4, h5, h6, .display-primary, .display-primary-6, .display-primary-5, .display-primary-4, .display-primary-3, .display-primary-2, .display-primary-1, .display-primary-0{
    font-family: $font-primary;
    text-transform: capitalize;
    letter-spacing: 2;
    line-height: 1.08;
  }

  h1, .display-primary-6{font-size: var(--fs13);letter-spacing: 2px;}
  h2, .display-primary-5{font-size: var(--fs11);letter-spacing: 1.5px;}
  h3, .display-primary-4{font-size: var(--fs09);letter-spacing: 1.25px;}
  h4, .display-primary-3{font-size: var(--fs07);letter-spacing: 1px;}
  h5, .display-primary-2{font-size: var(--fs05);letter-spacing: .75px;}
  h6, .display-primary-1{font-size: var(--fs04);letter-spacing: .5px;}
  .display-primary-0{font-size: var(--fs02);letter-spacing: .25px;}

  .display-secondary, .display-secondary-6, .display-secondary-5, .display-secondary-4, .display-secondary-3, .display-secondary-2, .display-secondary-1, .display-secondary-0, .display-tertiary, .display-tertiary-6, .display-tertiary-5, .display-tertiary-4, .display-tertiary-3, .display-tertiary-2, .display-tertiary-1, .display-tertiary-0{
      font-family: $font-secondary;
      font-weight: 300;
      text-transform: capitalize;
      letter-spacing: 0;
      line-height: 1.08;
  }

  .display-secondary-6, .display-tertiary-6{font-size: var(--fs10);}
  .display-secondary-5, .display-tertiary-5{font-size: var(--fs09);}
  .display-secondary-4, .display-tertiary-4{font-size: var(--fs07);}
  .display-secondary-3, .display-tertiary-3{font-size: var(--fs05);}
  .display-secondary-2, .display-tertiary-2{font-size: var(--fs04);}
  .display-secondary-1, .display-tertiary-1{font-size: var(--fs02);}
  .display-secondary-0, .display-tertiary-0{font-size: var(--fs01);}

  .label-primary-xl, .label-primary-lg, .label-primary-md, .label-primary-sm, .label-primary-xs{
    font-family: $font-primary;
    line-height: 1.08;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1.5px;
  }

  .label-primary-xl{font-size: var(--fs07);}
  .label-primary-lg{font-size: var(--fs05);}
  .label-primary-md{font-size: var(--fs04);}
  .label-primary-sm{font-size: var(--fs02);}
  .label-primary-xs{font-size: var(--fs01);}

  .label-secondary-xl, .label-secondary-lg, .label-secondary-md, .label-secondary-sm, .label-secondary-xs{
    font-family: $font-secondary;
    line-height: 1.08;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 1px;
  }

  .label-secondary-xl{font-size: var(--fs05);}
  .label-secondary-lg{font-size: var(--fs04);}
  .label-secondary-md{font-size: var(--fs02);}
  .label-secondary-sm{font-size: var(--fs01);}
  .label-secondary-xs{font-size: var(--fs00);}


  .button, .button-xl, .button-lg, .button-md, .button-sm, .button-xs{
    font-family: $font-primary;
    text-transform: uppercase;
    font-weight:700;
    letter-spacing:1.5px;
  }

  .button-xl{font-size: var(--fs07);}
  .button-lg{font-size: var(--fs05);}
  .button-md{font-size: var(--fs04);}
  .button-sm{font-size: var(--fs02);}
  .button-xs{font-size: var(--fs01);}

  body, p, .body, .body-xs, .body-sm, .body-md, .body-lg, .body-xl{
    font-family: $font-secondary;
    font-weight: 300;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
  }

  .body-sm, .body-xs{
    font-weight: 400;
  }

  .body-xl, .body-link-xl {font-size: var(--fs07);}
  .body-lg, .body-link-lg {font-size: var(--fs04);}
  .body-md, .body-link-md, p {font-size: var(--fs02);}
  .body-sm, .body-link-sm {font-size: var(--fs01);}
  .body-xs, .body-link-xs {font-size: var(--fs00);}

  .body-link, .body-link-xl, .body-link-lg, .body-link-md, .body-link-sm, .body-link-xs{
    font-weight:700;
    cursor: pointer;
    opacity:1;
    color: var(--primary-brand);
    &:hover{
      opacity:.5;
    }
  }
}
