section.hero {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  	background-image: url(https://images.amcnetworks.com/sundancechannel.com/wp-content/uploads/2019/01/State-of-the-Union-20x9-FINAL-2400x1080.jpg);
  	-webkit-background-size: cover;
  	height:0;
  	padding-top:56.25%;
  	background-size: cover;
  	background-position: center center;
	.hero-content{
		z-index:1;
		background: linear-gradient(0deg, rgba(0,0,0,.6) 0%, rgba(0,0,0,0) 100%);
		padding-bottom:2rem;
	}
}

#app-wrap.light .hero-content{
  z-index:1;
  background: linear-gradient(0deg, rgba(255,255,255,.6) 0%, rgba(255,255,255,0) 100%);
  padding-bottom:2rem;
}

@media screen and (max-width:960px){
	section.hero{
	    padding-top: 100%;
	    background-size: cover;
	    background-position: top center;
	    background-repeat: no-repeat;
	}
}
